import { FC } from 'react';
import { clsx } from 'clsx';

import { ButtonProps, ButtonVariant, ButtonScope } from './button.interface';

import { borderRadius } from './../../../tailwind/styles/border-radius';
import { focusRing } from './../../../tailwind/styles/focus';

export const Button: FC<ButtonProps> = ({
  variant = ButtonVariant.Primary,
  label,
  isLoading = false,
  children,
  disabled = false,
  ...props
}: ButtonProps) => {
  const loading = isLoading ? (
    <i className="mr-zep-0_5" data-testid="loading-icon">
      loading icon
    </i>
  ) : (
    ''
  );

  return (
    <button
      className={clsx(
        ButtonScope,
        `zep-${variant}`,
        borderRadius,
        focusRing,
        'focus-within:ring-offset-2',
        'text-zep-base',
        'leading-zep-1_25',
        'px-zep-1',
        'py-zep-0_75',
        { 'disabled cursor-not-allowed': disabled },
      )}
      {...props}
    >
      {loading}
      {label || children}
    </button>
  );
};
